import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import { ParallaxProvider } from "react-scroll-parallax";
import OnVisible from "react-on-visible";
import SectionCarousel1 from "@aem/marketing/marketing_common/SectionCarousel1";
import PropTypes from "prop-types";

const MSectionCarousel1Config = {
  emptyLabel: "MSectionCarousel1 Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MSectionCarousel1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      promotion: {
        className: "instagram mt-md-0",
        title: this.props.title || "",
        cards: [],
        fbLink: "",
        lineLink: "",
        displayContent: this.props.socialMediaTitle || "",
      },
    };
  }

  componentDidMount() {
    let newPromotion = Object.assign(this.state.promotion);
    newPromotion.cards = this.props.random
      ? this.randomCards(this.props.cards) || []
      : this.props.cards || [];
    newPromotion.fbLink = window.location.pathname || "";
    newPromotion.lineLink = window.location.pathname || "";
    this.setState({ promotion: newPromotion });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.random !== this.props.random) {
      let newPromotion = Object.assign(this.state.promotion);
      newPromotion.cards = this.props.random
        ? this.randomCards(this.props.cards) || []
        : this.props.cards || [];
      this.setState({ promotion: newPromotion });
    }
  }

  randomCards(cards) {
    if (!cards && !cards.length) return;
    const copyCards = JSON.parse(JSON.stringify(cards));

    return copyCards.sort(() => Math.random() - 0.5);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.appear && (
          <div
            id={this.props.id || ""}
            className="position-relative"
            style={{
              backgroundSize: "cover",
              backgroundPosition: "top",
              backgroundRepeat: "no-repeat",
              backgroundColor: `${
                this.props.bgColor ? this.props.bgColor : "#fafafa"
              }`,
              backgroundImage: `${
                this.props.topBg ? `url(${this.props.topBg})` : ""
              }`,
            }}
          >
            <OnVisible>
              <ParallaxProvider>
                <SectionCarousel1 {...this.state.promotion} />
              </ParallaxProvider>
            </OnVisible>
            <div
              className="bottom-bg"
              style={
                !!this.props.bottomBg
                  ? {
                      backgroundImage: `url(${this.props.bottomBg})`,
                    }
                  : null
              }
            />
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_section_carousel1")(
  MSectionCarousel1,
  MSectionCarousel1Config
);

MSectionCarousel1.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 社群媒體標題
   */
  socialMediaTitle: PropTypes.string,
  /**
   * 是否隨機播放
   */
  random: PropTypes.bool,
  /**
   * 卡牌CF回傳資料
   */
  cards: PropTypes.arrayOf(
    PropTypes.shape({
      images: PropTypes.string,
      title: PropTypes.string,
      description: PropTypes.string,
      link: PropTypes.string,
      target: PropTypes.string,
      alt: PropTypes.string,
      fetKey: PropTypes.string,
    })
  ),
};
