import { reducers } from "./reducer";
import { createStore } from "redux";

const store =
  typeof window !== "undefined"
    ? createStore(
        reducers,
        window.__PRELOADED_STATE__,
        window.__REDUX_DEVTOOLS_EXTENSION__ &&
          window.__REDUX_DEVTOOLS_EXTENSION__()
      )
    : createStore(reducers);
export default store;
