import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import LoadMore from "@ui/LoadMore";
import Button from "@ui/Button";
import Link from "@ui/Link";
import store from "@state";
import { showModal } from "@state/action";
import "@utils/copy-to-clipboard";
import { isMobile } from "@utils/device-utils";
import PropTypes from "prop-types";

const MSectionProductCardConfig = {
  emptyLabel: "MSectionProductCard Component",
  isEmpty: function (props) {
    return !props.title;
  },
};
export default class MSectionProductCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: {
        hasMore: true,
        list:
          this.props.list && this.props.list.length > 0 ? this.props.list : [],
      },
      loadSectionProduct:
        this.props.loadSectionProduct &&
        this.props.loadSectionProduct.length > 0
          ? this.props.loadSectionProduct
          : [],
      isMobile: false,
    };
  }
  componentDidMount() {
    this.setState({
      isMobile: isMobile() || false,
    });
  }
  ProductCardLoadMore = () => {
    let data = Object.assign(this.state.content);
    data["list"] = [
      ...this.state.content.list,
      ...this.state.loadSectionProduct,
    ];
    data["hasMore"] = false;
    console.log("data", data);
    this.setState({
      list: data,
    });
  };
  copyCodeToClipboard = (code) => {
    if (typeof window !== "undefined") {
      window.Clipboard.copy(code);
      store.dispatch(
        showModal({
          modalType: "lightBoxTextModal",
          modalProps: {
            currentModal: {
              content: `<p style="min-width:300px">已複製<span class="is-text-error">${code}</span></p>`,
              buttonText: "關閉",
            },
          },
        })
      );
    } else console.log("window is undefined");
  };
  render() {
    console.log("props", this.props);
    const {
      appear,
      id,
      bgColor,
      topBg,
      bottomBg,
      title,
      mPadding,
      wPadding,
    } = this.props;
    return (
      <React.Fragment>
        {appear && (
          <section
            id={id || ""}
            className="section-product-card position-relative card-fixed"
            style={{
              background: `${bgColor ? bgColor : ""} no-repeat top/cover url(${
                topBg || ""
              })`,
            }}
            ref={(node) => {
              if (node) {
                node.style.setProperty(
                  "padding",
                  this.state.isMobile ? mPadding : wPadding,
                  "important"
                );
              }
            }}
          >
            <div className="fui-container">
              <h2 dangerouslySetInnerHTML={{ __html: title }}></h2>
              <div className="fui-cards four-card is-product no-scrollbar py-0">
                {this.state.content.list.map((prod, i) => {
                  return (
                    <div key={prod.meta + i} className="fui-card is-product">
                      {prod.hottag ? (
                        <div className={`tag tag-${prod.hottag.color}`}>
                          {prod.hottag.text}
                        </div>
                      ) : null}
                      <div className="fui-card-image">
                        {!!prod.imgLink ? (
                          <Link to={prod.imgLink || ""} className="d-block">
                            <img
                              className="lazyload"
                              data-src={prod.image}
                              src={prod.image}
                              alt={prod.imgAlt}
                            />
                          </Link>
                        ) : (
                          <img
                            className="lazyload"
                            data-src={prod.image}
                            src={prod.image}
                            alt={prod.imgAlt}
                          />
                        )}
                      </div>
                      <div className="fui-card-caption">
                        <div className="fui-card-meta">{prod.meta}</div>
                        <div className="body">
                          <div
                            className="body"
                            dangerouslySetInnerHTML={{ __html: prod.name }}
                          ></div>
                        </div>
                        <div
                          className={`fui-card-price ${
                            prod.button && prod.button.name ? "" : "mb-0"
                          }`}
                        >
                          <div
                            className="body-1"
                            dangerouslySetInnerHTML={{ __html: prod.content }}
                          ></div>
                          {prod.coupon && prod.coupon.code && (
                            <div className="d-block body-1">
                              {prod.coupon.name}
                              <span
                                className="is-text-error"
                                style={{ cursor: "pointer" }}
                                onClick={() => {
                                  this.copyCodeToClipboard(prod.coupon.code);
                                }}
                              >
                                {prod.coupon.code}
                              </span>
                            </div>
                          )}
                        </div>
                        {prod.button && prod.button.name && (
                          <Button
                            btnStyle="secondary"
                            className="m-0 w-100"
                            link={prod.button.link}
                            target={prod.button.target}
                            data-fet-key={prod.button.fetKey}
                          >
                            {prod.button.name}
                          </Button>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>
              {this.state.loadSectionProduct.length > 0 && (
                <LoadMore
                  className="mb-0"
                  moreLabel="展開看更多"
                  noMoreLabel="已經沒有更多內容囉！"
                  load={this.state.content.hasMore}
                  click={() => this.ProductCardLoadMore()}
                />
              )}
            </div>
            {!!bottomBg && (
              <div
                className="bottom-bg"
                style={{
                  backgroundImage: `url(${bottomBg})`,
                }}
              />
            )}
          </section>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_section_product_card")(
  MSectionProductCard,
  MSectionProductCardConfig
);

MSectionProductCard.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 小網Padding
   */
  mPadding: PropTypes.string,
  /**
   * 大網Padding
   */
  wPadding: PropTypes.string,
  /**
   * 看更多
   */
  hasMore: PropTypes.bool,
  /**
   * 卡牌
   */
  list: PropTypes.arrayOf(
    PropTypes.shape({
      image: PropTypes.string,
      imgAlt: PropTypes.string,
      imgLink: PropTypes.string,
      meta: PropTypes.string,
      name: PropTypes.string,
      content: PropTypes.string,
      coupon: PropTypes.shape({
        code: PropTypes.string,
        name: PropTypes.string,
      }),
      button: PropTypes.shape({
        name: PropTypes.string,
        link: PropTypes.string,
        fetKey: PropTypes.string,
        target: PropTypes.string,
      }),
      hottag: PropTypes.shape({
        color: PropTypes.string,
        text: PropTypes.string,
      }),
    })
  ),
  /**
   * 更多卡牌
   */
  loadSectionProduct: PropTypes.arrayOf(
    PropTypes.shape({
      image: PropTypes.string,
      imgAlt: PropTypes.string,
      imgLink: PropTypes.string,
      meta: PropTypes.string,
      name: PropTypes.string,
      content: PropTypes.string,
      coupon: PropTypes.shape({
        code: PropTypes.string,
        name: PropTypes.string,
      }),
      button: PropTypes.shape({
        name: PropTypes.string,
        link: PropTypes.string,
        fetKey: PropTypes.string,
        target: PropTypes.string,
      }),
      hottag: PropTypes.shape({
        color: PropTypes.string,
        text: PropTypes.string,
      }),
    })
  ),
};
