import React from "react";

import Tooltip from "@ui/Tooltip";
import Tab from "@ui/tab/Tab";
import { Grid } from "@material-ui/core";
import Dropdown from "@ui/Dropdown";
import TabPane from "@ui/tab/TabPane";
import Slider from "react-slick";
import Button from "@ui/Button";
import PropTypes from "prop-types";

import store from "@state";
import { showModal, updateModalProps } from "@state/action";

import * as formatNumber from "@utils/numberFormatter";
require("./devicePlan.sass");

class DevicePlanTable extends React.Component {
  constructor(props) {
    super(props);
    this.planTable = React.createRef();
    this.modalForm = React.createRef();
    this.sliderRef = [];

    this.state = {
      isMobile: window.innerWidth < 960,
      titleHeight: 0,
      selectedList:
        this.props.programList && this.props.programList.length
          ? this.props.programList[0].text
          : "",
      defaultChecked: "",
      checkKey: 0,
      currentHover: -1,
      currentTab: this.props.tabs.default || 0,
      form: {
        filter: { value: "", required: false },
      },
    };
    this.slideSetting = {
      dots: false,
      infinite: false,
      arrows: true,
      draggable: true,
      responsive: [
        {
          breakpoint: 1200,
          settings: {
            arrows: true,
            slidesToShow: 3,
            variableWidth: true,
          },
        },
        {
          breakpoint: 768,
          settings: {
            arrows: true,
            slidesToShow: 2,
            variableWidth: true,
          },
        },
        {
          breakpoint: 480,
          settings: {
            arrows: true,
            slidesToShow: 1,
            variableWidth: true,
          },
        },
      ],
    };
    this.onChange = this.onChange.bind(this);
  }

  componentDidUpdate(prevProps, prevState) {
    const { modalType, modalProps } = store.getState().modalReducer;

    if (prevProps.tabs.default !== this.props.tabs.default) {
      this.setState({ currentTab: this.props.tabs.default || 0 });
    }

    // TODO: 暫解，只有 modalType 為 "devicePlanTable" 時才更新 modal props。
    if (
      modalType &&
      modalType === "devicePlanTable" &&
      modalProps.layout &&
      modalProps.layout === this.props.layout
    )
      store.dispatch(updateModalProps(this.createModalProps()));
  }

  setHover = (index) => {
    this.setState({
      currentHover: index,
    });
  };

  tabChange = (index) => {
    this.onChange("filter", "");
    this.setState({
      currentTab: index,
    });
  };

  setSlider = (ref) => {
    this.sliderRef.push(ref);
  };

  hasSelect = (tab) => {
    let index = 0;
    tab.forEach((item, i) => {
      if (this.props.selected.name === item.name) {
        index = i;
      }
    });

    return index;
  };
  onChange(name, value) {
    let form = Object.assign(this.state.form);
    form[name].value = value;
    this.setState({
      form: form,
    });
  }
  spec = (item, idx, e) => {
    this.setState({
      currentTab: idx,
      selectedList: e,
    });
    this.onChange("filter", "");
    this.props.resetFilterContent();
  };
  getTitleLength = (item) => {
    let length = 0;
    let height = 61;
    let hasBtn = false;
    for (let k = 0; k < item.length; k++) {
      if (item[k].action && item[k].action.text) {
        hasBtn = true;
        break;
      }
    }
    for (let i = 0; i < item.length; i++) {
      if (item[i].displayName) {
        let temp = item[i].displayName.replace(/[^\x00-\xff]/g, "xx").length;
        length = length < temp ? temp : length;
      }
    }
    switch (true) {
      case length > 21:
        hasBtn ? (height = 92) : (height = 62);
        break;
      case length > 13:
        height = 61;
        break;
      default:
        break;
    }
    return height;
  };
  updateDimensions = () => {
    this.setState({
      isMobile: window.innerWidth < 960,
    });
  };
  componentDidMount() {
    window.addEventListener("resize", this.updateDimensions);
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
  }
  getSelected = (item) => {
    let pickPlan = this.props.tabs.list[this.state.currentTab].pickPlan;
    let result = false;
    pickPlan.map((pick) => {
      if (item === pick) {
        result = true;
      }
    });
    return result;
  };
  clear = () => {
    this.onChange("filter", "");
    this.setState({
      defaultChecked: "",
    });
    setTimeout(() => {
      this.setState({
        checkKey: this.state.checkKey + 1,
      });
    }, 100);
    this.forceUpdate();
  };

  createModalProps = () => {
    return {
      modalType: "devicePlanTable",
      modalProps: {
        layout: this.props.layout,
        state: this.state,
        props: this.props,
        refs: {
          modalForm: this.modalForm,
        },
        methods: {
          clear: this.clear,
          onChange: this.onChange,
        },
        events: {
          onValid: () => {
            this.setState({ formCanSubmit: true });
          },
          onInvalid: () => {
            this.setState({ formCanSubmit: false });
          },
        },
      },
    };
  };

  render() {
    return (
      <section
        className={`fui-plan-table-container device-plan-table ${
          this.props.className ? this.props.className : ""
        }`}
      >
        <div className="fui-container">
          <div dangerouslySetInnerHTML={{ __html: this.props.title }}></div>
          {this.props.layout &&
          this.props.programList &&
          this.props.programList.length
            ? (this.props.layout == "dropdown" && (
                <Grid container>
                  <Grid item xs={12} sm={12} md={5}>
                    <div className="device-table-dropdown mb-md-2 mb-0">
                      <h3>{this.props.filterTitle}</h3>
                      <Dropdown
                        className="is-button"
                        list={this.props.programList}
                        label={this.state.selectedList}
                        arrow={true}
                        hasCheck={false}
                        onChange={(item, idx, e) => this.spec(item, idx, e)}
                      />
                    </div>
                  </Grid>
                </Grid>
              )) ||
              (this.props.layout == "tab" && (
                <Tab
                  list={this.props.programList}
                  name="fui-plan-table-tab"
                  default={this.state.currentTab}
                  onChange={this.tabChange}
                />
              ))
            : null}

          {this.props.tabContent.map((tab, i) => (
            <TabPane
              active={i === this.state.currentTab}
              key={`fui-plan-table-${i}`}
            >
              <div className="fui-plan-table is-promotion-device">
                <div className="fui-plan-table-header">
                  <div
                    className="fui-plan-table-item"
                    style={{
                      height:
                        this.getTitleLength(tab) +
                        (this.state.isMobile ? 110 : 145),
                    }}
                  >
                    <div
                      className="fui-plan-table-label d-flex flex-column"
                      style={{ height: "100%" }}
                    >
                      {this.props.tabs.list[this.state.currentTab].filterName}
                      <Button
                        onClick={() => {
                          this.setState(
                            {
                              defaultChecked: this.state.form.filter.value,
                            },
                            () => {
                              store.dispatch(
                                showModal(this.createModalProps())
                              );
                            }
                          );
                        }}
                        size="small"
                        btnStyle="secondary"
                        className="plan-table-btn mt-2"
                        reverse={true}
                      >
                        篩選
                      </Button>
                    </div>
                  </div>
                  {this.props.tableTitle[i].map((item, k) => {
                    return (
                      //htmlEditor
                      <div key={item + k} className="fui-plan-table-item">
                        <div
                          className="fui-plan-table-label"
                          dangerouslySetInnerHTML={{
                            __html: item,
                          }}
                        ></div>
                      </div>
                    );
                  })}
                </div>
                <div className="fui-plan-table-body" ref={this.planTable}>
                  <Slider
                    {...this.slideSetting}
                    initialSlide={this.hasSelect(tab)}
                    slidesToShow={tab.length >= 4 ? 4 : tab.length}
                    ref={this.setSlider}
                  >
                    {tab.map((item, k) => (
                      <div
                        className={`fui-plan-table-segment  ${
                          this.getSelected(item.name) ? "is-select" : ""
                        } `}
                        key={`fui-plan-table-${i}-segment-${k}`}
                      >
                        <div className="fui-plan-table-title">
                          <h5 style={{ height: this.getTitleLength(tab) }}>
                            {item.displayName}
                          </h5>
                          <h3
                            className={`heading-1
                              ${item.action && item.action.text ? "" : "mb-0"}
                            `}
                          >
                            <small className="mr-1">{item.unit}</small>
                            <span className="price mr-0 is-text-error">
                              <span>$</span>
                              {formatNumber.default(item.title)}
                            </span>
                          </h3>
                          {item.action && item.action.text ? (
                            <Button
                              data-fet-key={item.action.fetKey || ""}
                              target={item.action.target || "_self"}
                              link={item.action.link}
                              btnStyle={
                                this.getSelected(item.name)
                                  ? "primary"
                                  : "secondary"
                              }
                            >
                              {item.action.text}
                            </Button>
                          ) : null}
                        </div>
                        {item.detail && item.detail.length
                          ? item.detail.map((col, idx) => (
                              <div
                                onMouseOut={(e) => this.setHover(-1)}
                                onMouseOver={(e) => this.setHover(idx)}
                                className={`fui-plan-table-item ${
                                  this.state.currentHover === idx
                                    ? "is-hover"
                                    : ""
                                }`}
                              >
                                <div>
                                  <span
                                    dangerouslySetInnerHTML={{
                                      __html: col.content,
                                    }}
                                  ></span>
                                  {col.tooltip ? (
                                    <Tooltip
                                      parentNode={null}
                                      content={
                                        <i className="icon-information"></i>
                                      }
                                      trigger="click"
                                      tooltip={col.tooltip}
                                    />
                                  ) : null}
                                </div>
                              </div>
                            ))
                          : null}
                        {item.compare && item.compare.length
                          ? item.compare.map((col, idx) => (
                              <div
                                onMouseOut={(e) => this.setHover(-1)}
                                onMouseOver={(e) => this.setHover(idx)}
                                className={`fui-plan-table-item ${
                                  this.state.currentHover === idx
                                    ? "is-hover"
                                    : ""
                                }`}
                              >
                                <div className="diff">
                                  {col.map((colItem, j) => (
                                    <div className="diff-item">
                                      <span
                                        dangerouslySetInnerHTML={{
                                          __html: colItem.content,
                                        }}
                                      ></span>
                                      {!!colItem.tooltip ? (
                                        <Tooltip
                                          parentNode={null}
                                          content={
                                            <i className="icon-information"></i>
                                          }
                                          trigger="click"
                                          tooltip={colItem.tooltip}
                                        />
                                      ) : null}
                                    </div>
                                  ))}
                                </div>{" "}
                              </div>
                            ))
                          : null}
                      </div>
                    ))}
                  </Slider>
                </div>
              </div>
            </TabPane>
          ))}
        </div>
      </section>
    );
  }
}

DevicePlanTable.propTypes = {
  title: PropTypes.string,
  filterTitle: PropTypes.string,
  className: PropTypes.string,
  tabs: PropTypes.shape({
    list: PropTypes.arrayOf(
      PropTypes.shape({
        labe: PropTypes.string,
      })
    ),
    scroll: PropTypes.bool,
  }),
  tabContent: PropTypes.array,
  filterArr: PropTypes.array,
  programList: PropTypes.array,
  selected: PropTypes.object,
  onChange: PropTypes.func,
  filtering: PropTypes.func,
  resetFilterContent: PropTypes.func,
};

export default DevicePlanTable;
