import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import FindItemBanner from "@aem/marketing/marketing_common/banner/FindItemBanner";
import PropTypes from "prop-types";
const MFindItemBannerConfig = {
  emptyLabel: "MFindItemBanner Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MFindItemBanner extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { appear, id, title, topBg, bottomBg, bgColor, image } = this.props;
    return (
      <React.Fragment>
        {appear && (
          <div
            style={{
              background: `${bgColor ? bgColor : ""} no-repeat top/cover url(${
                topBg || ""
              })`,
            }}
            id={id}
          >
            <FindItemBanner
              title={title || ""}
              image={{
                md: image.md || "",
                sm: image.sm || "",
              }}
            />
            <div
              className="bottom-bg"
              style={{
                backgroundImage: `url(${bottomBg || ""})`,
              }}
            />
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_find_item_banner")(
  MFindItemBanner,
  MFindItemBannerConfig
);

MFindItemBanner.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 圖片回傳資料
   */
  image: PropTypes.shape({
    md: PropTypes.string,
    sm: PropTypes.string,
  }),
};
