import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import SocialMedia from "@ui/SocialMedia";
import PropTypes from "prop-types";

const MSocialMediaConfig = {
  emptyLabel: "MSocialMedia Component",
  isEmpty: function (props) {
    return !props.content;
  },
};

export default class MSocialMedia extends Component {
  constructor(props) {
    super(props);
    this.state = {
      locationLink: "",
    };
  }
  componentDidMount() {
    this.setState({
      locationLink: window.$history.location.pathname,
    });
  }
  render() {
    const { appear, id, bgColor, topBg, bottomBg, theme, content } = this.props;
    return (
      <React.Fragment>
        {appear && (
          <div
            id={id || ""}
            className="pb-md-4 pb-3 pt-1 position-relative"
            style={{
              background: `${
                bgColor ? bgColor : "#fafafa"
              } no-repeat top/cover url(${topBg || ""})`,
            }}
          >
            <div className="fui-container" style={{ zIndex: "1" }}>
              <SocialMedia
                theme={theme || "light"}
                fbLink={this.state.locationLink}
                lineLink={this.state.locationLink}
                size={48}
                displayContent={content || ""}
              />
            </div>
            {!!bottomBg && (
              <div
                className="bottom-bg"
                style={{
                  backgroundImage: `url(${bottomBg})`,
                }}
              />
            )}
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_social_media")(
  MSocialMedia,
  MSocialMediaConfig
);

MSocialMedia.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 顏色樣式選擇 - [dark,light]
   */
  theme: PropTypes.string,
  /**
   * 內容說明
   */
  content: PropTypes.string,
};
