import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import SingleVideo from "@ui/SingleVideo";
import PropTypes from "prop-types";

const MSingleVideoConfig = {
  emptyLabel: "MSingleVideo Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MSingleVideo extends Component {
  constructor(props) {
    super(props);
    this.VideoSlideSetting = {
      dots: false,
      infinite: false,
      arrows: true,
      slidesToShow: 1,
      draggable: true,
    };
    this.state = {
      currentVideo: "",
      modalOpen: false,
    };
  }
  openVideoModal = (data) => {
    console.log(`data:`, data);
    this.setState({
      modalOpen: true,
      alterVideo: data.alterVideo ? data.alterVideo : null,
      currentVideo: data.videoId,
    });
  };

  closeModal = () => {
    this.setState({
      modalOpen: false,
      alterVideo: "",
      currentVideo: "",
    });
  };

  render() {
    const { appear, id, title, bgColor, topBg, bottomBg, video } = this.props;
    return appear ? (
      <div
        className="position-relative"
        style={{
          padding: "5px 0",
          background: `${bgColor ? bgColor : ""} no-repeat top/cover url(${
            topBg || ""
          })`,
        }}
      >
        <div id={id || ""} className="fui-container" style={{ zIndex: 2 }}>
          <div className="paper">
            <div
              className="section-title m-0 p-0"
              dangerouslySetInnerHTML={{ __html: title }}
            ></div>
            <SingleVideo
              fetKey={video.fetKey || ""}
              videoId={video.videoId || ""}
              imgSmall={video.imgSmall || ""}
              imgLarge={video.imgLarge || ""}
              alterVideo={video.alterVideo || ""}
            />
          </div>
        </div>
        {!!bottomBg && (
          <div
            className="bottom-bg"
            style={{
              backgroundImage: `url(${bottomBg})`,
            }}
          />
        )}
      </div>
    ) : null;
  }
}

MapTo("cbu/components/marketing/m_single_video")(
  MSingleVideo,
  MSingleVideoConfig
);

MSingleVideo.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string.isRequired,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 影片內容
   */
  video: PropTypes.shape({
    alterVideo: PropTypes.bool,
    fetKey: PropTypes.string,
    videoId: PropTypes.string,
    imgLarge: PropTypes.string,
    title: PropTypes.string,
    content: PropTypes.string,
    imgSmall: PropTypes.string,
  }),
};
