import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import PanelInformation from "@ui/panelContent/PanelInformation";
import PropTypes from "prop-types";

const MPanelInformationConfig = {
  emptyLabel: "MPanelInformation Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MPanelInformation extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.appear ? (
          <div
            id={this.props.id || ""}
            className="pb-md-4 pb-3 pt-1 position-relative"
            style={{
              background: `${
                this.props.bgColor ? this.props.bgColor : ""
              } no-repeat top/cover url(${this.props.topBg || ""})`,
            }}
          >
            <section className="form-section">
              <div className="fui-container">
                <PanelInformation
                  className="mb-4"
                  bgColor="#e4e4e4"
                  open={this.props.open}
                  title={this.props.title}
                  content={this.props.content}
                />
              </div>
            </section>
            <div
              className="bottom-bg"
              style={
                !!this.props.bottomBg
                  ? {
                      backgroundImage: `url(${this.props.bottomBg})`,
                    }
                  : null
              }
            />
          </div>
        ) : null}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_panel_information")(
  MPanelInformation,
  MPanelInformationConfig
);

MPanelInformation.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 預先開啟
   */
  open: PropTypes.bool,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 回傳CF資料
   */
  content: PropTypes.string,
};
