import { TYPES } from "../action";
import { logoutInitialState } from "../state";

export default function loginReducer(state = logoutInitialState, action) {
  switch (action.type) {
    case TYPES.SET_LOGIN_DATA:
      return Object.assign({}, state, { ...action.data });
    case TYPES.SET_LOGOUT_DATA:
      return Object.assign({}, state, { ...action.data });
    default:
      return state;
  }
}
