import { TYPES } from "../action";
import { modalInitialState } from "../state";

export default function modalReducer(state = modalInitialState, action) {
  switch (action.type) {
    case TYPES.SHOW_MODAL:
      return Object.assign({}, state, {
        show: true,
        ...action,
      });

    case TYPES.UPDATE_MODAL_PROPS: 
      return Object.assign({}, state, {
        ...action,
      });

    case TYPES.HIDE_MODAL:
      return modalInitialState;

    default:
      return state;
  }
}
