import React from "react";

import Formsy from "formsy-react";
import CheckboxButtons from "@ui/form/CheckboxButtons";
import Button from "@src/components/Button";

const DevicePlanTableModal = ({
  closeModal,
  state,
  props,
  refs,
  methods,
  events,
}) => {
  return (
    <div className="fui-prompt-modal-body p-4">
      <Formsy onValid={events.onValid} onInvalid={events.onInvalid}>
        <p
          onClick={() => {
            methods.clear();
          }}
          className="m-0 clean"
        >
          清除全部
        </p>
        <CheckboxButtons
          ref={refs.modalForm}
          label={`篩選方案 (${
            state.form.filter.value.length !== 0
              ? state.form.filter.value.split(",").length
              : 0
          }/${props.tabs.list[state.currentTab].filterArr.length})`}
          name="filter"
          checkKey={state.checkKey}
          default={state.defaultChecked}
          options={props.tabs.list[state.currentTab].filterArr}
          onChange={methods.onChange}
        />
        <div className="page-action">
          <Button
            onClick={() => {
              props.filtering({
                filter: state.form.filter,
                tabContent: props.tabContent[state.currentTab],
                index: state.currentTab,
                arr: props.tabs.list[state.currentTab].filterArr,
              });
              closeModal();
            }}
            className={`mb-lg-0 mb-2 ${state.formCanSubmit ? "" : "disabled"}`}
            btnStyle="primary"
          >
            確認篩選
          </Button>
          <Button onClick={closeModal} className="mb-0" btnStyle="secondary">
            取消
          </Button>
        </div>
      </Formsy>
    </div>
  );
};

export default DevicePlanTableModal;
