import React, { Component } from "react";
import Button from "@ui/Button";
import Slider from "react-slick";
import PropTypes from "prop-types";

class CardRecommend extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <section
        style={{
          background: `no-repeat top/cover url(${this.props.topBg || ""})`,
        }}
      >
        <div
          className={`fui-container mt-md-0 mt-4 ${
            this.props.className ? this.props.className : ""
          }`}
        >
          <div dangerouslySetInnerHTML={{ __html: this.props.title }}></div>
          <Slider
            {...{
              dots: false,
              infinite: false,
              arrows: true,
              slidesToShow: 3,
              draggable: true,
              responsive: [
                {
                  breakpoint: 960,
                  settings: {
                    arrows: true,
                    slidesToShow: 1,
                    variableWidth: true,
                  },
                },

                {
                  breakpoint: 768,
                  settings: {
                    arrows: true,
                    slidesToShow: 2,
                    variableWidth: true,
                  },
                },
                {
                  breakpoint: 480,
                  settings: {
                    arrows: true,
                    slidesToShow: 1,
                    variableWidth: true,
                  },
                },
              ],
            }}
            className="horizontal-cards is-program pb-9"
          >
            {this.props.program.map((card, idx) => (
              <div key={idx} className="fui-card is-card-program">
                {card.tag ? (
                  <div className={`tag tag-${card.tag.color}`}>
                    {card.tag.text}
                  </div>
                ) : null}
                <img
                  src={process.env.PUBLIC_URL + card.icon}
                  alt={card.iconAlt}
                />
                <div dangerouslySetInnerHTML={{ __html: card.name }}></div>
                <div dangerouslySetInnerHTML={{ __html: card.title }}></div>
                <div
                  dangerouslySetInnerHTML={{
                    __html: card.description || card.list,
                  }}
                ></div>
                {card.button.length > 1 ? (
                  <div className="form-group two-buttons">
                    <Button
                      btnStyle="primary"
                      size="medium"
                      link={card.button[0].link}
                      target={card.button[0].target}
                      data-fet-Key={card.button[0].fetKey}
                    >
                      {card.button[0].title}
                    </Button>
                    <Button
                      btnStyle="secondary"
                      size="medium"
                      link={card.button[1].link}
                      target={card.button[1].target}
                      data-fet-Key={card.button[1].fetKey}
                    >
                      {card.button[1].title}
                    </Button>
                  </div>
                ) : (
                  <div className="form-group m-0">
                    <Button
                      btnStyle="primary"
                      size="medium"
                      className="mb-0"
                      link={card.button[0].link}
                      target={card.button[0].target}
                      data-fet-key={card.button[0].fetKey}
                    >
                      {card.button[0].title}
                    </Button>
                  </div>
                )}
              </div>
            ))}
          </Slider>
        </div>
        <div
          className="bottom-bg"
          style={
            this.props.bottomBg
              ? { backgroundImage: `url(${this.props.bottomBg})` }
              : null
          }
        />
      </section>
    );
  }
}
CardRecommend.propTypes = {
  title: PropTypes.string,
  className: PropTypes.string,
  program: PropTypes.array,
  bottomBg: PropTypes.string,
  topBg: PropTypes.string,
};
export default CardRecommend;
