import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import PanelContainer1 from "@aem/marketing/marketing_common/panel/PanelContainer1";
import PropTypes from "prop-types";

const MPanelContentConfig = {
  emptyLabel: "MPanelContent Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MPanelContent extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.appear && (
          <div
            id={this.props.id || ""}
            className="pb-md-4 pb-3 pt-1 position-relative"
            style={{
              background: `${
                this.props.bgColor ? this.props.bgColor : ""
              } no-repeat top/cover url(${this.props.topBg || ""})`,
            }}
          >
            <PanelContainer1
              title={this.props.title}
              content={this.props.content}
            ></PanelContainer1>
            <div
              className="bottom-bg"
              style={
                !!this.props.bottomBg
                  ? {
                      backgroundImage: `url(${this.props.bottomBg})`,
                    }
                  : null
              }
            />
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_panel_content")(
  MPanelContent,
  MPanelContentConfig
);

MPanelContent.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 內文
   */
  content: PropTypes.string,
};
