import React from "react";
import Button from "@src/components/Button";

const NoticeModal = ({
  closeModal,
  modalContent,
  modalTitle,
  modalButtons,
}) => {
  return (
    <div className="fui-modal-content">
      <div className="fui-modal-head">
        <span dangerouslySetInnerHTML={{ __html: modalTitle }}></span>
      </div>
      <div
        className="fui-modal-body"
        dangerouslySetInnerHTML={{ __html: modalContent }}
      ></div>
      <div
        className={`fui-modal-foot ${
          modalButtons && modalButtons.length > 1 ? "two-buttons" : ""
        }`}
      >
        {modalButtons && modalButtons.length ? (
          modalButtons[0] && modalButtons[0].text ? (
            <Button btnStyle="primary" onClick={closeModal}>
              {modalButtons[0].text}
            </Button>
          ) : null
        ) : null}

        {modalButtons && modalButtons.length > 1 ? (
          modalButtons[1] && modalButtons[1].text ? (
            <Button btnStyle="secondary" onClick={closeModal}>
              {modalButtons[1].text}
            </Button>
          ) : null
        ) : null}
      </div>
    </div>
  );
};

export default NoticeModal;
