import React from "react";

import Button from "@src/components/Button";

const FormImageUploadModal = ({
  closeModal,
  modelContent,
  actions,
  events
}) => {
  return (
    <div className="fui-prompt-modal-body">
      {modelContent.type === "wrongFile" ? <React.Fragment>
        <h4>{modelContent.title}</h4>
        <p>{modelContent.desc}</p>
        <div className="fui-buttons">
          <Button btnStyle="primary" onClick={closeModal}>
            確定
        </Button>
        </div>
      </React.Fragment> : null}
      {modelContent.type === "deleteFile" ? <React.Fragment>
        <h4>{modelContent.title}</h4>
        <p>{modelContent.desc}</p>
        <div className="fui-buttons">
          <Button btnStyle="primary" onClick={() => { actions.confirmDelete(); closeModal() }}>
            確定
        </Button>
          <Button
            btnStyle="secondary"
            onClick={() => { events.deleteCancel(); closeModal() }}
          >
            取消
        </Button>
        </div>
      </React.Fragment> : null}
      {modelContent.type === "uploadFail" ? <React.Fragment>
        <h4>{modelContent.title}</h4>
        <p>{modelContent.desc}</p>
        <div className="fui-buttons">
          <Button btnStyle="primary" onClick={closeModal}>
            確定
        </Button>
        </div>
      </React.Fragment> : null}
    </div>
  );
};

export default FormImageUploadModal;
