import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import NavTab from "@ui/partials/NavTab";
import PropTypes from "prop-types";

const MNavTabConfig = {
  emptyLabel: "MNavTab Component",
  isEmpty: function (props) {
    return !props || !props.pageTitle;
  },
};

export default class MNavTab extends Component {
  constructor(props) {
    super(props);
    let query = new URLSearchParams({ tab: "" });
    if (typeof window !== "undefined") {
      query = new URLSearchParams(window.$history.location.search);
    }
    this.state = {
      currentTab:
        query.get("tab") === "cbu" ? 1 : query.get("tab") === "contact" ? 2 : 1,
      isMobile: typeof window !== "undefined" && window.innerWidth < 768,
    };
  }

  tabChange = (index) => {
    this.setState({
      currentTab: index,
    });
  };

  render() {
    return (
      <NavTab
        style={{
          display: "flex",
          alignContent: "center",
          flexWrap: "wrap",
        }}
        pageTitle={this.props.pageTitle}
        onChange={this.tabChange}
        default={0}
        tabs={
          this.props.tabs && this.props.tabs.list.length > 0
            ? this.props.tabs
            : { list: [] }
        }
        buttons={this.props.button}
      />
    );
  }
}

MapTo("cbu/components/marketing/m_nav_tab")(MNavTab, MNavTabConfig);

MNavTab.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 標題
   */
  pageTitle: PropTypes.string,
  /**
   * 頁籤
   */
  tabs: {
    name: PropTypes.string,
    list: PropTypes.arrayOf(
      PropTypes.shape({
        link: PropTypes.string,
        target: PropTypes.string,
        fetkey: PropTypes.string,
        label: PropTypes.string,
      })
    ),
  },
  /**
   * 按鈕
   */
  button: PropTypes.arrayOf(
    PropTypes.shape({
      link: PropTypes.string,
      target: PropTypes.string,
      fetkey: PropTypes.string,
      text: PropTypes.string,
      btnStyle: PropTypes.string,
      type: PropTypes.string,
      modal: PropTypes.shape({
        modalTitle: PropTypes.string,
        modalContent: PropTypes.string,
        closeText: PropTypes.string,
      }),
    })
  ),
};
