import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import PropTypes from "prop-types";

const MSectionArticleConfig = {
  emptyLabel: "MSectionArticle Component",
  isEmpty: function (props) {
    return !props || !props.title;
  },
};

export default class MSectionArticle extends Component {
  render() {
    const { appear, id, bgColor, topBg, bottomBg, title, content } = this.props;
    return (
      <React.Fragment>
        {appear && (
          <div
            id={id || ""}
            className={`position-relative py-3 ${
              !!topBg ? null : "article-panel"
            }`}
            style={{
              background: `${bgColor ? bgColor : ""} no-repeat top/cover url(${
                topBg || ""
              })`,
              margin: 0,
            }}
          >
            <div
              className="fui-container fui-panel px-0"
              style={{ zIndex: "1", background: "none" }}
            >
              <div
                className="mt-0"
                dangerouslySetInnerHTML={{ __html: title || "" }}
              ></div>
              <div dangerouslySetInnerHTML={{ __html: content || "" }}></div>
            </div>

            {!!bottomBg && (
              <div
                className="bottom-bg"
                style={{
                  left: 0,
                  backgroundImage: `url(${bottomBg})`,
                }}
              />
            )}
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_section_article")(
  MSectionArticle,
  MSectionArticleConfig
);
MSectionArticle.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 內文
   */
  content: PropTypes.string,
};
