import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import PropTypes from "prop-types";

const MAddValueCardConfig = {
  emptyLabel: "MAddValueCard Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MAddValueCard extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.appear && (
          <div
            id={this.props.id || ""}
            className="pb-md-4 pb-3 pt-1 position-relative"
            style={{
              backgroundSize: "cover",
              backgroundPosition: "top",
              backgroundRepeat: "no-repeat",
              backgroundColor: `${
                this.props.bgColor ? this.props.bgColor : "#fafafa"
              }`,
              backgroundImage: `${
                this.props.topBg ? `url(${this.props.topBg})` : ""
              }`,
            }}
          >
            <main className="prepaid">
              {/* <div className="ribbon-bg"> */}
              <section id="whyPrepaid" className="why-prepaid">
                <div className="fui-container">
                  <div
                    className="head heading-2"
                    dangerouslySetInnerHTML={{
                      __html: this.props.title || "",
                    }}
                  ></div>
                  <ul>
                    {this.props.itemList.length > 0 &&
                      this.props.itemList.map((item, i) => {
                        return (
                          <li key={i}>
                            <img
                              src={process.env.PUBLIC_URL + item.img}
                              alt={item.alt}
                            />
                            <div className="info">
                              <div
                                dangerouslySetInnerHTML={{
                                  __html: item.title,
                                }}
                              ></div>
                              <div
                                dangerouslySetInnerHTML={{
                                  __html: item.content,
                                }}
                              ></div>
                            </div>
                          </li>
                        );
                      })}
                  </ul>
                </div>
              </section>
              {/* </div> */}
            </main>
            <div
              className="bottom-bg"
              style={
                !!this.props.bottomBg
                  ? {
                      backgroundImage: `url(${this.props.bottomBg})`,
                    }
                  : null
              }
            />
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_add_value_card")(
  MAddValueCard,
  MAddValueCardConfig
);

MAddValueCard.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 大標題
   */
  title: PropTypes.string,
  /**
   * cf回傳資料
   */
  itemList: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      content: PropTypes.string,
      img: PropTypes.string,
      alt: PropTypes.string,
    })
  ),
};
