import React from "react";
import Button from "@src/components/Button";

const FormSubmitModal = ({
  closeModal,
  content,
  btnName,
  successCallbackFn,
}) => {
  return (
    <div className="fui-prompt-modal-body">
      <div
        dangerouslySetInnerHTML={{
          __html: content || "",
        }}
      ></div>
      <div className="page-action">
        <Button
          onClick={() => {
            closeModal(successCallbackFn);
          }}
          btnStyle="primary"
        >
          {btnName || "確認"}
        </Button>
      </div>
    </div>
  );
};

export default FormSubmitModal;
