import React from "react";
import Button from "@src/components/Button";

const RBannerCardsModal = ({ closeModal, currentModal }) => {
  return (
    <div className="fui-prompt-modal-body">
      <h5 className="align-center">{currentModal.title}</h5>
      <p class="align-left mt-0">
        {currentModal.prodType && currentModal.prodType === "D"
          ? "適用國家及費率"
          : "上網費率"}
      </p>
      <div class="fui-table-response mb-2 mb-lg-4">
        <table class="modal-roaming-country-table">
          <thead>
            <tr>
              <th>
                {currentModal.prodType && currentModal.prodType === "D"
                  ? "國家"
                  : "用量"}
              </th>
              <th>上網費率</th>
            </tr>
          </thead>
          <tbody>
            {currentModal.prodType && currentModal.prodType === "D"
              ? currentModal.content.countryList &&
                currentModal.content.countryList.length &&
                currentModal.content.countryList.map((tr) => {
                  return (
                    <tr>
                      <td>{tr.CON_LIST_MIX || ""}</td>
                      <td>{tr.CON_LIST_MIX_PRC || ""}</td>
                    </tr>
                  );
                })
              : currentModal.content.dataPrices &&
                currentModal.content.dataPrices.length &&
                currentModal.content.dataPrices.map((tr) => {
                  return tr.dataPrice ? (
                    <tr>
                      <td>{tr.dataVolume || ""}</td>
                      <td>{tr.dataPrice || ""}</td>
                    </tr>
                  ) : null;
                })}
          </tbody>
        </table>
      </div>
      {currentModal.content.applyCannel ? (
        <div class="align-left mb-3 notice">
          <p class="is-text-medium mb-1">申請管道</p>
          <ul
            class="green-dot-list"
            dangerouslySetInnerHTML={{
              __html: currentModal.content.applyCannel || "",
            }}
          ></ul>
        </div>
      ) : null}
      <div class="align-left mb-3 notice">
        <p class="is-text-medium mb-1">注意事項</p>
        <p
          dangerouslySetInnerHTML={{
            __html: currentModal.content.roamingNotice || "",
          }}
        ></p>
      </div>

      <div className="fui-buttons justify-center">
        <Button btnStyle="primary" className="" onClick={closeModal}>
          我瞭解了
        </Button>
      </div>
    </div>
  );
};

export default RBannerCardsModal;
