import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import CardMultiSteps from "@aem/marketing/marketing_common/card/CardMultiSteps";
import OnVisible from "react-on-visible";
import PropTypes from "prop-types";

const MCardMultiStepsConfig = {
  emptyLabel: "MCardMultiSteps Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MCardMultiSteps extends Component {
  constructor(props) {
    super(props);
    this.state = {
      credit: this.props.bgColor
        ? this.props.bgColor
        : "/resources/cbu/img/cbu-goshare-bg-yellow3.png",
      steps: {
        title: this.props.title || "",
        hasMore: this.props.stepsLoadData.length > 0 ? true : false,
        list:
          this.props.list && this.props.list.length > 0 ? this.props.list : [],
        stepsLoadDataLength: this.props.stepsLoadData.length,
      },
      stepsLoadData:
        this.props.stepsLoadData && this.props.stepsLoadData.length > 0
          ? this.props.stepsLoadData
          : [],
    };
  }
  // call API to get steps' data
  loadMore = () => {
    let data = Object.assign(this.state.steps);
    data["list"] = [...this.state.steps.list, ...this.state.stepsLoadData];
    data["hasMore"] = false;
    this.setState({
      list: data,
    });
  };
  render() {
    const { appear, id, bgColor, topBg, bottomBg } = this.props;
    return (
      <React.Fragment>
        {appear && (
          <OnVisible id={id || ""}>
            <div
              className="pb-md-4 pb-3 pt-1 position-relative"
              style={{
                background: `${
                  bgColor ? bgColor : ""
                } no-repeat top/cover url(${topBg || ""})`,
              }}
            >
              <CardMultiSteps
                // bg={this.state.credit}
                {...this.state.steps}
                loadMore={this.loadMore}
              />
              {bottomBg && (
                <div
                  className="bottom-bg"
                  style={{
                    backgroundImage: `url(${bottomBg})`,
                  }}
                />
              )}
            </div>
          </OnVisible>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_card_multisteps")(
  MCardMultiSteps,
  MCardMultiStepsConfig
);

MCardMultiSteps.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 大標題
   */
  title: PropTypes.string,
  /**
   * 更多顯示
   */
  hasMore: PropTypes.bool,
  /**
   * 初始顯示CF資料
   */
  list: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      content: PropTypes.string,
      bg: PropTypes.string,
    })
  ),
  /**
   * 顯示更多CF資料
   */
  stepsLoadData: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      content: PropTypes.string,
      bg: PropTypes.string,
    })
  ),
};
