import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import SectionCollapseInfo from "@ui/partials/collapse/SectionCollapseInfo";
import PropTypes from "prop-types";

const MSectionCollapseInfoConfig = {
  emptyLabel: "MSectionCollapseInfo Component",
  isEmpty: function (props) {
    return !props || !props.title || !props.content;
  },
};

export default class MSectionCollapseInfo extends Component {
  render() {
    const {
      appear,
      open,
      id,
      title,
      content,
      collapseStyle,
      containerStyle,
      theme,
      bgColor,
      margin,
    } = this.props;

    return appear && content ? (
      <SectionCollapseInfo
        open={open}
        id={id}
        margin={margin}
        title={title}
        className={collapseStyle}
        container={containerStyle}
        theme={theme ? theme : "light"}
        bgColor={bgColor}
        content={content || ""}
      ></SectionCollapseInfo>
    ) : null;
  }
}

MapTo("cbu/components/marketing/m_section_collapse_info")(
  MSectionCollapseInfo,
  MSectionCollapseInfoConfig
);

MSectionCollapseInfo.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 預先開啟
   */
  open: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 邊框高度
   */
  margin: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 顏色樣式選擇
   */
  theme: PropTypes.string,
  /**
   * 元件高度選擇
   * (低版 / 寬版)
   */
  collapseStyle: PropTypes.string,
  /**
   * 內文樣式選擇
   * (窄版 / 寬版)
   */
  containerStyle: PropTypes.bool,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 開合內文
   */
  content: PropTypes.string,
};
