import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import OnVisible from "react-on-visible";
import $http from "@api/banner";
import loadable from "@loadable/component";
import PropTypes from "prop-types";

const CardFeature = loadable(() =>
  import("@aem/marketing/marketing_common/card/CardFeature")
);

const MCardFeatureConfig = {
  emptyLabel: "MCardFeature Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MCardFeature extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.appear && (
          <div
            id={this.props.id || ""}
            className="pb-md-4 pb-3 pt-1 position-relative"
            style={{
              backgroundSize: "cover",
              backgroundPosition: "top",
              backgroundRepeat: "no-repeat",
              backgroundColor: `${
                this.props.bgColor ? this.props.bgColor : ""
              }`,
              backgroundImage: `${
                this.props.topBg ? `url(${this.props.topBg})` : ""
              }`,
            }}
          >
            <OnVisible>
              {this.props.cards.length > 0 ? (
                <div className="feature-card">
                  <CardFeature
                    {...{
                      title: this.props.title,
                      cards: this.props.cards,
                    }}
                  />
                </div>
              ) : null}
            </OnVisible>
            <div
              className="bottom-bg"
              style={
                !!this.props.bottomBg
                  ? {
                      backgroundImage: `url(${this.props.bottomBg})`,
                    }
                  : null
              }
            />
          </div>
        )}
      </React.Fragment>
    );
  }
}
function loadData(WrappedComponent) {
  return class extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        cards: [],
      };
    }
    async componentDidMount() {
      if (!this.props.cfForderPath || String(this.props.appear) === "false")
        return;
      let params = {
        path: this.props.cfForderPath,
        tags: this.props.tags,
        limit: 20,
      };
      /* eslint-disable */
      if (typeof bttf !== "undefined") {
        params.bttf = bttf;
      }
      /* eslint-enable */
      const rawData = await $http.getMCardFeatureCF(params);
      if (rawData.success && rawData.result) {
        rawData.result = rawData.result.map((item) => {
          if (item.type === "card") {
            item.type = "card01";
          }
          return item;
        });

        this.setState({
          cards: rawData.result,
        });
      }
    }
    render() {
      return <WrappedComponent {...this.props} {...this.state} />;
    }
  };
}
MapTo("cbu/components/marketing/m_card_feature")(
  loadData(MCardFeature),
  MCardFeatureConfig
);

MCardFeature.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 透過API取得的CF資料結構
   */
  cards: PropTypes.arrayOf(
    PropTypes.shape({
      image: PropTypes.string,
      bg: PropTypes.string,
      alt: PropTypes.string,
      description: PropTypes.string,
      fetKey: PropTypes.string,
      action: PropTypes.shape({
        btnStyle: PropTypes.string,
        link: PropTypes.string,
        text: PropTypes.string,
        target: PropTypes.string,
      }),
      title: PropTypes.string,
      type: PropTypes.string,
    })
  ),
};
