import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import Panel from "@ui/panel/Panel";
import PanelTab from "@ui/panelContent/PanelTab";
import PropTypes from "prop-types";

const MPanelTabBoxConfig = {
  emptyLabel: "MPanelTabBox Component",
  isEmpty: function (props) {
    return !props.title;
  },
};

export default class MPanelTabBox extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tabs: [],
    };
  }

  createTabArray = (contents) => {
    if (contents.length)
      return contents.map((item, i) => {
        return {
          name: `panel-tab-${i + 1}`,
          label: item.tab,
        };
      });
    return [];
  };

  componentDidMount() {
    this.setState({
      tabs: this.createTabArray(this.props.contents),
    });
  }

  render() {
    const {
      appear,
      id,
      title,
      contents,
      bgColor,
      topBg,
      bottomBg,
    } = this.props;

    return appear ? (
      <section
        id={id}
        style={{
          background: `${bgColor ? bgColor : ""} no-repeat top/cover url(${
            topBg || ""
          })`,
        }}
      >
        <div className="fui-container">
          <Panel>
            <div
              dangerouslySetInnerHTML={{
                __html: title,
              }}
            ></div>
            {this.state.tabs && this.state.tabs.length ? (
              <PanelTab
                activeTab={0}
                tabs={{
                  name: "panel-tab",
                  default: 0,
                  list: this.state.tabs,
                }}
              >
                {contents &&
                  contents.length &&
                  contents.map((item) => {
                    return (
                      <div
                        dangerouslySetInnerHTML={{
                          __html: item.content,
                        }}
                      ></div>
                    );
                  })}
              </PanelTab>
            ) : null}
          </Panel>
        </div>
        <div
          className="bottom-bg"
          style={bottomBg ? { backgroundImage: `url(${bottomBg})` } : null}
        />
      </section>
    ) : null;
  }
}

MapTo("cbu/components/marketing/m_panel_tab_box")(
  MPanelTabBox,
  MPanelTabBoxConfig
);

MPanelTabBox.propTypes = {
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 標題
   */
  title: PropTypes.string,
  /**
   * 頁籤&內文資料
   */
  contents: PropTypes.arrayOf(
    PropTypes.shape({
      tab: PropTypes.string,
      content: PropTypes.string,
    })
  ),
};
