import React, { Component } from "react";
import Modal from "react-modal";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import store from "@state";
import { hideModal } from "@state/action";

import { default as modalTypes } from "./modals";

const mapStateToProps = (state) => ({
  ...state.modalReducer,
});

const MODAL_COMPONENTS = {
  bannerPromotion: {
    component: modalTypes.bannerPromotion,
    containerClassName: "fui-app-trial-modal",
  },
  notice: {
    component: modalTypes.notice,
    containerClassName: "fui-notice-modal",
  },
  appTrail: {
    component: modalTypes.appTrail,
    containerClassName: "fui-app-trial-modal",
  },
  devicePlanTable: {
    component: modalTypes.devicePlanTable,
    containerClassName: "fui-prompt-modal device-filter",
  },
  cardRoamingMulti: {
    component: modalTypes.cardRoamingMulti,
    containerClassName: "fui-prompt-modal",
  },
  formSubmit: {
    component: modalTypes.formSubmit,
    containerClassName: "fui-prompt-modal",
  },
  formImageUploadModal: {
    component: modalTypes.formImageUploadModal,
    containerClassName: "fui-prompt-modal",
  },
  lightBoxTextModal: {
    component: modalTypes.lightBoxTextModal,
    containerClassName: "fui-app-trial-modal",
  },
  rBannerCardsModal: {
    component: modalTypes.rBannerCardsModal,
    containerClassName: "fui-prompt-modal-body",
  },
  rBannerCardsEsimModal: {
    component: modalTypes.rBannerCardsEsimModal,
    containerClassName: "fui-prompt-modal-body",
  },
  phoneModal: {
    component: modalTypes.phoneModal,
    containerClassName: "fui-notice-modal",
  },
};

export class MModal extends Component {
  constructor(props) {
    super(props);
    if (!props) return;

    const { show, modalProps = {} } = props;

    this.state = {
      modalOpen: show || false,
      htmlContent: modalProps.htmlContent || "",
      customTemplate: modalProps.customTemplate || false,
    };
  }

  componentDidUpdate(prevProps) {
    const { show, modalProps = {} } = this.props;

    if (this.props.show !== prevProps.show) {
      this.setState({
        modalOpen: show,
        htmlContent: modalProps.htmlContent || "",
        customTemplate: modalProps.customTemplate || false,
      });
    }
  }

  componentDidMount() {
    this.unlisten = this.props.history.listen((location, action) => {
      store.dispatch(hideModal());
    });
  }

  componentWillUnmount() {
    this.unlisten();
  }

  render() {
    if (
      !this.props.modalType ||
      !this.props.modalProps ||
      !MODAL_COMPONENTS[this.props.modalType]
    )
      return null;

    const SpecifiedModal = MODAL_COMPONENTS[this.props.modalType].component;

    return (
      <Modal
        onAfterOpen={(e) =>
          document.getElementsByTagName("html")[0].classList.add("modal-open")
        }
        onAfterClose={(e) =>
          document
            .getElementsByTagName("html")[0]
            .classList.remove("modal-open")
        }
        isOpen={this.state.modalOpen}
        contentLabel="Global Modal"
        className={
          MODAL_COMPONENTS[this.props.modalType].containerClassName || ""
        }
        parentSelector={
          this.props.parentSelector ?
            () => document.querySelector(this.props.parentSelector) || document.body :
            () => document.body
        }
      >
        {this.state.customTemplate ? (
          <div
            className="fui-app-trial-body"
            dangerouslySetInnerHTML={{
              __html: this.state.htmlContent || this.children,
            }}
          ></div>
        ) : (
          <SpecifiedModal
            closeModal={(callbackFn) => {
              store.dispatch(hideModal());
              if (callbackFn && typeof callbackFn === "function") callbackFn();
            }}
            {...this.props.modalProps}
          />
        )}
      </Modal>
    );
  }
}

export default connect(mapStateToProps, null)(withRouter(MModal));
