import React from "react";
import Button from "@ui/Button";
import PropTypes from "prop-types";

class Panel extends React.Component {
  render() {
    return (
      <div className="fui-container" style={{ zIndex: 1 }}>
        <div
          className={`fui-panel ${
            this.props.className ? this.props.className : ""
          } ${this.props.initial ? "no-shadow no-bg" : ""}`}
          style={
            this.props.bgColor ? { backgroundColor: this.props.bgColor } : null
          }
          ref={(node) => {
            if (node && this.props.padding) {
              node.style.setProperty(
                "padding",
                this.props.padding,
                "important"
              );
            }
          }}
        >
          <div
            dangerouslySetInnerHTML={{
              __html: this.props.content || "",
            }}
          />
          <div className="page-action mb-1">
            {this.props.btnList.map((item, i) => {
              return (
                <Button
                  data-fet-key={item.fetKey}
                  key={i}
                  btnStyle="secondary"
                  size="large"
                  link={item.link}
                  target={item.target}
                >
                  {item.text}
                </Button>
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}

Panel.propTypes = {
  title: PropTypes.string,
  content: PropTypes.string,
  className: PropTypes.string,
  bgColor: PropTypes.string,
  initial: PropTypes.bool,
  btnList: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      link: PropTypes.string,
    })
  ),
};

export default Panel;
