import { TYPES } from "../action";
import { categoryInitialState } from "../state";

export default function lifeCircleReducer(
  state = categoryInitialState,
  action
) {
  switch (action.type) {
    case TYPES.SET_LIFECIRCLE_CATEGORY_PROPS:
      sessionStorage.setItem("categoryId", action.categoryId);
      return Object.assign({}, state, {
        ...action.categoryId,
      });
    default:
      return state;
  }
}
