import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import PanelContainer6 from "@aem/marketing/marketing_common/panel/PanelContainer6";
import PropTypes from "prop-types";

const MPanelContentButtonPaddingConfig = {
  emptyLabel: "MPanelContentButtonPadding Component",
  isEmpty: function (props) {
    return !props.result;
  },
};

export default class MPanelContentButtonPadding extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile: typeof window !== "undefined" && window.innerWidth < 960,
    };
  }

  render() {
    return (
      <React.Fragment>
        {this.props.appear && (
          <div
            id={this.props.id || ""}
            className="pb-md-4 pb-3 pt-1 position-relative"
            style={{
              background: `${
                this.props.bgColor ? this.props.bgColor : ""
              } no-repeat top/cover url(${this.props.topBg || ""})`,
            }}
          >
            {this.props.result && (
              <PanelContainer6
                padding={
                  this.state.isMobile
                    ? this.props.mPadding
                    : this.props.wPadding
                }
                initial={true}
                content={this.props.result.content}
                btnList={this.props.result.buttonList || []}
              ></PanelContainer6>
            )}
            <div
              className="bottom-bg"
              style={
                !!this.props.bottomBg
                  ? {
                      backgroundImage: `url(${this.props.bottomBg})`,
                    }
                  : null
              }
            />
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_panel_content_button_padding")(
  MPanelContentButtonPadding,
  MPanelContentButtonPaddingConfig
);

MPanelContentButtonPadding.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 小網padding(例: 20px 20px 20px 20px)
   */
  mPadding: PropTypes.string,
  /**
   * 大網padding(例: 20px 20px 20px 20px)
   */
  wPadding: PropTypes.string,
  /**
   * CF回傳資料
   */
  result: PropTypes.shape({
    content: PropTypes.string,
    buttonList: PropTypes.arrayOf(
      PropTypes.shape({
        text: PropTypes.string,
        link: PropTypes.string,
        target: PropTypes.string,
        fetKey: PropTypes.string,
      })
    ),
  }),
};
