import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import CardRecommend from "@aem/marketing/marketing_common/card/CardRecommend";
import OnVisible, { setDefaultProps } from "react-on-visible";
import $http from "@api/marketingCard";
import PropTypes from "prop-types";

setDefaultProps({
  bounce: true,
  percent: 20,
});

const MCardRecommendConfig = {
  emptyLabel: "MCardRecommend Component",
  isEmpty: function (props) {
    return !props || !props.title;
  },
};

export default class MCardRecommend extends Component {
  render() {
    const {
      id,
      appear,
      title,
      bgColor,
      topBg,
      bottomBg,
      mcardRecommendCardsList,
    } = this.props;
    return appear ? (
      <OnVisible id={id}>
        {mcardRecommendCardsList ? (
          <div
            style={{
              position: "relative",
              backgroundColor: bgColor || "",
            }}
          >
            <CardRecommend
              title={title}
              program={mcardRecommendCardsList}
              topBg={topBg}
              bottomBg={bottomBg}
            ></CardRecommend>
          </div>
        ) : null}{" "}
      </OnVisible>
    ) : null;
  }
}

function loadData(WrappedComponent) {
  return class extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        mcardRecommendCardsList: [],
      };
    }
    async componentDidMount() {
      if (!this.props.cfForderPath || String(this.props.appear) === "false")
        return;
      let params = {
        path: this.props.cfForderPath,
        tagId: this.props.tagId,
      };
      const rawData = await $http.getMCards("MCardRecommend", params);
      if (rawData.success && rawData.result) {
        this.setState({
          mcardRecommendCardsList: rawData.result.mcardRecommendCardsList,
        });
      }
    }
    render() {
      return <WrappedComponent {...this.props} {...this.state} />;
    }
  };
}

MapTo("cbu/components/marketing/m_card_recommend")(
  loadData(MCardRecommend),
  MCardRecommendConfig
);

MCardRecommend.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 大標題
   */
  title: PropTypes.string,
  /**
   * 透過API取得的卡牌CF資料
   */
  mcardRecommendCardsList: PropTypes.arrayOf(
    PropTypes.shape({
      icon: PropTypes.string,
      iconAlt: PropTypes.string,
      name: PropTypes.string,
      title: PropTypes.string,
      list: PropTypes.string,
      tag: PropTypes.shape({
        color: PropTypes.string,
        text: PropTypes.string,
      }),
      button: PropTypes.arrayOf(
        PropTypes.shape({
          link: PropTypes.string,
          fetKey: PropTypes.string,
          title: PropTypes.string,
          target: PropTypes.string,
        })
      ),
    })
  ),
};
