import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import { ErrorBoundary } from "@aem/error/ErrorBoundary";
import loadable from "@loadable/component";

const NavAnchor = loadable(() => import("@ui/partials/NavAnchor"));
const CbuNavAnchorConfig = {
  emptyLabel: "CbuNavAnchor Component",
  isEmpty: function (props) {
    return !props || !props.pageTitle;
  },
};

export default class CbuNavAnchor extends Component {
  render() {
    return (
      <ErrorBoundary>
        <NavAnchor
          pageTitle={this.props.pageTitle || ""}
          button={{
            link: this.props.button.link || "#",
            text: this.props.button.text || "",
            target: this.props.button.target || "_self",
          }}
          redButton={{
            link: this.props.redButton.link || "#",
            text: this.props.redButton.text || "",
            target: this.props.redButton.target || "_self",
          }}
          tabs={this.props.tabs || []}
        />
      </ErrorBoundary>
    );
  }
}

MapTo("cbu/components/header/nav_anchor")(CbuNavAnchor, CbuNavAnchorConfig);
