import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import BannerPromotionBasic from "@aem/marketing/marketing_common/banner/BannerPromotionBasic";
import PropTypes from "prop-types";

const MBannerPromotionBasicConfig = {
  emptyLabel: "MBannerPromotionBasic Component",
  isEmpty: function (props) {
    return !props.slider.length > 0;
  },
};

export default class MBannerPromotionBasic extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.appear && this.props.slider.length > 0 && (
          <div
            id={this.props.id || ""}
            className="position-relative"
            style={{
              backgroundSize: "cover",
              backgroundPosition: "top",
              backgroundRepeat: "no-repeat",
              backgroundColor: `${
                this.props.bgColor ? this.props.bgColor : "#fafafa"
              }`,
              backgroundImage: `${
                this.props.topBg ? `url(${this.props.topBg})` : ""
              }`,
            }}
          >
            <BannerPromotionBasic
              autoplay={this.props.autoplay}
              bgColor="#fff"
              slides={this.props.slider}
              openModal={this.openModal}
            />
            <div
              className="bottom-bg"
              style={
                !!this.props.bottomBg
                  ? {
                      backgroundImage: `url(${this.props.bottomBg})`,
                    }
                  : null
              }
            />
          </div>
        )}
      </React.Fragment>
    );
  }
}

MapTo("cbu/components/marketing/m_banner_promotion_basic")(
  MBannerPromotionBasic,
  MBannerPromotionBasicConfig
);

MBannerPromotionBasic.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 是否輪播
   */
  autoplay: PropTypes.bool,
  /**
   * CF回傳資料
   */
  slider: PropTypes.arrayOf(
    PropTypes.shape({
      images: PropTypes.shape({
        md: PropTypes.string,
        alt: PropTypes.string,
        sm: PropTypes.string,
      }),
      bgColor: PropTypes.string,
      description: PropTypes.string,
      theme: PropTypes.string,
      tag: PropTypes.string,
      title: PropTypes.string,
      actions: PropTypes.arrayOf(
        PropTypes.shape({
          modalContent: PropTypes.string,
          btnStyle: PropTypes.string,
          link: PropTypes.string,
          fetKey: PropTypes.string,
          text: PropTypes.string,
          type: PropTypes.string,
          target: PropTypes.string,
        })
      ),
    })
  ),
};
