import React, { Component } from "react";
import { MapTo } from "@utils/funcation-utils";
import DevicePlanTable from "@aem/marketing/marketing_common/table/DevicePlanTable";
import PropTypes from "prop-types";

const MDevicePlanTableTabConfig = {
  emptyLabel: "MDevicePlanTableTab Component",
  isEmpty: function (props) {
    return !props || !props.planTabs.title;
  },
};

export default class MDevicePlanTableTab extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filterStatus: false,
    };
  }

  componentDidMount() {
    this.setState({ original: JSON.parse(JSON.stringify(this.props)) });
    this.setExclusiveData();
  }

  setExclusiveData = () => {
    const planMock = JSON.parse(JSON.stringify(this.props));
    let activePlan = 0;
    let activeTab = 0;
    let activeNum = 0;
    this.setState({
      filterStatus: true,
      query: "",
      defaultTab: activeNum,
      navTabs: planMock.navTab,
      selectedPlan: planMock.plan[activePlan],
      planTabs: {
        title: planMock.planTabs.title,
        tableTitle: planMock.planTabs.tableTitle,
        tabs: {
          scroll: true,
          list: planMock.planTabs.tabs.map((tab) => {
            return {
              label: tab.name,
              filterArr: tab.filterArr,
              filterName: tab.filterName,
              pickPlan: tab.pickPlan,
            };
          }),
          default: activeTab,
        },
        tabContent: planMock.plan,
      },
    });
  };

  // 根據DevicePlanTable的選擇來打API //
  // tableOnChange = (val) => {
  //   // this.getExclusiveData(val).then((result) => {
  //   //   PlanMock = result;
  //   //   this.setExclusiveData();
  //   // });
  //   this.setExclusiveData();
  // };
  filtering = (val) => {
    const planMock = JSON.parse(JSON.stringify(this.state.original));
    if (!!val.filter.value) {
      let filterArr = [];
      let active = [];
      let second = [];
      let final = [];

      val.filter.value.split(",").map((key) => {
        filterArr.push(val.arr[key].label);
      });

      val.tabContent.map((item) => {
        if (this.contains(filterArr, item.name)) {
          active.push(item);
        } else {
          second.push(item);
        }
      });

      let planTab = JSON.parse(JSON.stringify(this.state.planTabs.tabContent));
      final = [...active, ...second];

      //檢查是否有重複的資料
      // var result = new Set();
      // var repeat = new Set();
      // final.forEach((item) => {
      //   result.has(item) ? repeat.add(item) : result.add(item);
      // });
      // final = [...result];
      // console.log(final);

      let activeTab = 0;

      planTab[val.index] = final;

      this.setState({
        filterStatus: true,
        planTabs: {
          title: planMock.planTabs.title,
          tableTitle: planMock.planTabs.tableTitle,
          tabs: {
            scroll: true,
            list: planMock.planTabs.tabs.map((tab, i) => {
              if (i === val.index) {
                return {
                  label: tab.name,
                  filterArr: tab.filterArr,
                  filterName: tab.filterName,
                  pickPlan: filterArr,
                };
              } else {
                return {
                  label: tab.name,
                  filterArr: tab.filterArr,
                  filterName: tab.filterName,
                  pickPlan: tab.pickPlan,
                };
              }
            }),
            default: activeTab,
          },
          tabContent: planTab,
        },
      });
    } else {
      this.resetFilterContent();
    }
  };

  contains(array, obj) {
    for (var i = 0; i < array.length; i++) {
      if (array[i] === obj) {
        return true;
      }
    }
    return false;
  }

  resetFilterContent = () => {
    this.setExclusiveData();
    this.forceUpdate();
  };
  render() {
    const { bgColor, topBg, bottomBg } = this.props;
    return this.props.appear && this.state.filterStatus ? (
      <div
        id={this.props.id}
        style={{
          background: `${bgColor ? bgColor : ""} no-repeat top/cover url(${
            topBg || ""
          })`,
          position: "relative",
        }}
      >
        <DevicePlanTable
          layout="tab"
          filterTitle="規格"
          {...this.state.planTabs}
          filterArr={this.state.filterArr}
          resetFilterContent={this.resetFilterContent}
          programList={this.state.original.programList}
          selected={this.state.selectedPlan}
          filtering={(val) => {
            this.filtering(val);
          }}
          // onChange={(val) => this.tableOnChange(val)}
        />
        <div
          className="bottom-bg"
          style={
            bottomBg
              ? {
                  backgroundImage: `url(${bottomBg})`,
                }
              : null
          }
        />
      </div>
    ) : null;
  }
}

MapTo("cbu/components/marketing/m_device_plan_table_tab")(
  MDevicePlanTableTab,
  MDevicePlanTableTabConfig
);

MDevicePlanTableTab.propTypes = {
  /**
   * 顯示
   */
  appear: PropTypes.bool,
  /**
   * 錨點名稱
   */
  id: PropTypes.string,
  /**
   * 背景顏色
   */
  bgColor: PropTypes.string,
  /**
   * 背景圖片(上)
   */
  topBg: PropTypes.string,
  /**
   * 背景圖片(下)
   */
  bottomBg: PropTypes.string,
  /**
   * 大標題
   */
  title: PropTypes.string,
  /**
   * 頁籤
   */
  programList: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      label: PropTypes.string,
      value: PropTypes.string,
    })
  ),
  /**
   * 表格欄位
   */
  planTabs: PropTypes.shape({
    tabs: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string,
        filterName: PropTypes.string,
        pickPlan: PropTypes.array,
        child: PropTypes.array,
        filterArr: PropTypes.arrayOf(
          PropTypes.shape({
            label: PropTypes.string,
            value: PropTypes.string,
          })
        ),
      })
    ),
    title: PropTypes.string,
    label: PropTypes.string,
    value: PropTypes.string,
  }),
  /**
   * 表格資料
   */
  plan: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      unit: PropTypes.string,
      displayName: PropTypes.string,
      name: PropTypes.string,
      action: PropTypes.shape({
        link: PropTypes.string,
        fetKey: PropTypes.string,
        text: PropTypes.string,
        target: PropTypes.string,
      }),
      detail: PropTypes.arrayOf(
        PropTypes.shape({
          name: PropTypes.string,
          tooltip: PropTypes.string,
          content: PropTypes.string,
        })
      ),
    })
  ),
};
