import React from "react";
import Slider from "react-slick";
import Button from "@ui/Button";
import store from "@state";
import { showModal } from "@state/action";

import PropTypes from "prop-types";

class BannerPromotionBasic extends React.Component {
  constructor(props) {
    super(props);

    this.slideSetting = {
      dots: true,
      infinite: this.props.autoplay ? this.props.autoplay : false,
      arrows: false,
      slidesToShow: 1,
      autoplay: this.props.autoplay ? this.props.autoplay : false,
      autoplaySpeed: this.props.autoplaySpeed ? this.props.autoplaySpeed : 5000,
      draggable:
        typeof this.props.draggable === "undefined"
          ? true
          : this.props.draggable,
      adaptiveHeight: true,
      afterChange: (current) => this.setState({ activeSlide: current }),
    };
    this.state = {
      modalOpen: false,
      activeSlide: 0,
    };
  }

  setModalContent = (content) => {
    store.dispatch(
      showModal({
        modalType: "bannerPromotion",
        modalProps: {
          htmlContent: content,
        },
      })
    );
  };

  getThemeColor = () => {
    let current = this.state.activeSlide;
    return this.props.slides[current].theme === "dark" ? "dark-theme" : "";
  };

  render() {
    return (
      <div
        className={`fui-banner is-exclusive is-promotion ${this.getThemeColor()} ${
          this.props.className ? this.props.className : ""
        }`}
      >
        <Slider {...this.slideSetting}>
          {this.props.slides.map((slide, i) => (
            <div
              className={`estore-banner-item ${
                slide.theme === "dark" ? "dark-theme" : ""
              }`}
              key={`estore-banner-${i}`}
            >
              <div
                className="bg"
                style={
                  slide.bgColor
                    ? { backgroundColor: slide.bgColor }
                    : { backgroundColor: "#fff" }
                }
              >
                <img
                  src={slide.image.md}
                  alt={slide.image.alt}
                  className="d-none d-sm-block"
                />
                <img
                  src={slide.image.sm}
                  alt={slide.image.alt}
                  className="d-block d-sm-none"
                />
              </div>
              <div className="caption">
                <div className="fui-container">
                  <div className="caption-wrapper">
                    {!!slide.tag ? (
                      <div className="tag">
                        <div className="tag-bg"></div>
                        {slide.tag}
                      </div>
                    ) : null}
                    <div
                      dangerouslySetInnerHTML={{
                        __html: slide.title,
                      }}
                    />
                    <div
                      className="description"
                      dangerouslySetInnerHTML={{
                        __html: slide.description,
                      }}
                    />
                    <div className="action p-0">
                      {slide.actions.map((act, j) =>
                        act.text ? (
                          act.type === "link" ? (
                            <Button
                              link={act.link}
                              target={act.target}
                              btnStyle={act.btnStyle}
                              data-fet-key={act.fetKey}
                              key={`estore-banner-${i}-btn-${j}`}
                              size="large"
                            >
                              {act.text}
                            </Button>
                          ) : (
                            <Button
                              onClick={(e) =>
                                this.setModalContent(act.modalContent)
                              }
                              btnStyle={act.btnStyle}
                              data-fet-key={act.fetKey}
                              key={`estore-banner-${i}-btn-${j}`}
                              size="large"
                            >
                              {act.text}
                            </Button>
                          )
                        ) : null
                      )}
                    </div>
                  </div>
                </div>
              </div>

              <div className="fui-arrow d-block d-sm-none">
                <i className="icon-chevron-down"></i>
              </div>
            </div>
          ))}
        </Slider>
      </div>
    );
  }
}

BannerPromotionBasic.propTypes = {
  slides: PropTypes.arrayOf(
    PropTypes.shape({
      image: PropTypes.shape({
        md: PropTypes.string,
        sm: PropTypes.string,
      }),
      theme: PropTypes.string,
      tag: PropTypes.string,
      title: PropTypes.string,
      description: PropTypes.string,
      actions: PropTypes.arrayOf(
        PropTypes.shape({
          text: PropTypes.string,
          link: PropTypes.string,
          target: PropTypes.string,
        })
      ),
    }).isRequired
  ),
  className: PropTypes.string,
  bgColor: PropTypes.string,
  openModal: PropTypes.func,
};

export default BannerPromotionBasic;
